body {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}

.title {
  margin: 0;
  width: 100%;
  height: 125px;

  display: flex;
  align-items: center;
  justify-content: center;
  color: #07376e;
  font-size: 4.5vmin;
}
span {
  color: #f36b3b;
}

.footer {
  width: 100%;
  min-height: 200px;
  background-color: #07376e;
  color: whitesmoke;
}
.footercontainer {
  padding: 20px 20px;
  display: flex;
  flex-wrap: wrap;

  align-items: center;
  justify-content: space-evenly;
}

.section_11 {
  margin-top: 0px;
  width: 45vmin;
  border-radius: 10px;
  background-color: white;
  padding: 10px 20px;
}
.section_1 {
  padding-top: 10px;
  margin-bottom: 20px;
}
.section_1 h3 {
  padding-bottom: 5px;
}
.section_1 p {
  font-size: 2.46vmin;
  padding: 5px;
}
