* {
  box-sizing: border-box;
  font-family: sans-serif;
}
body {
  margin: 0;
  padding: 0;
}

.org_heading_container {

 
  height: 10vh;
  background-color: #f36b3b;
  color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  flex-wrap: wrap;
}

.Organization_Container_main_image img {
  width: 60vmin;
  border-radius: 20px;
  width: 60vmin;
  border-radius: 20px;
  height: 30vh;
 
}
.org_heading_container_projectName,
.org_heading_container_OrgName {
  padding: 2px 20px;
  font-size: 2.6vmin;
}
.Organization_Container {
  margin-top: 50px;
  padding: 20px 50px;
}
.Organization_Container_main {
  background-color: #f5f5f5;
  border-radius: 20px;
  padding: 20px 20px;

  min-height: 50vh;
 
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.Organization_Container_main_content {
  
  margin-top: 10px;
  width: 70vmin;
}
.Organization_Container_main_content P {
  font-size: 2.7vmin;
  line-height: 1.5;
  text-align: justify;
}

/*____________________________________weather updates Starting______________________________________________________________*/

.weather_cards_title{
  margin-top: 80px ;
  margin-bottom: 40px;
  color: #f36b3b;
  text-align:center ;
  font-size: 4.4vmin;
}

.weather_cards {
  width: 100%;
  min-height: 80vh;
  padding: 20px 6vmin;
}
.weather_cards_main {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.weather_card {
  min-width: 250px;
  min-height: 27vh;
  background-image:linear-gradient(#197b96,#0341a5);
padding: 1vmin;
  margin: 18px 17px;

  border-radius: 25px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color:whitesmoke
}
.weather_card h3 {
  font-size: 3vmin;
}
.weather_card img {
  margin-top: 6px;
  width: 100px;
  height: 100px;
}
